body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  background: -webkit-repeating-linear-gradient(
    45deg,
    #F8BBD0,
    #F8BBD0 30px,
    #FCE4EC 30px,
    #FCE4EC 100px
  );
  background: -o-repeating-linear-gradient(
    45deg,
    #F8BBD0,
    #F8BBD0 30px,
    #FCE4EC 30px,
    #FCE4EC 100px
  );
  background: repeating-linear-gradient(
    45deg,
    #F8BBD0,
    #F8BBD0 30px,
    #FCE4EC 30px,
    #FCE4EC 100px
  );
  min-height:100vh;
  min-width:100%;
}

.text-grey {
  color: #9E9E9E;
}

.text-center {
  text-align: center;
}



